.search-and-result {
    display:flex;
    flex-direction:row;
    align-items:center;
    padding: 0 5vw;
    & input {
        width: 65%;
        height: 5vw;
        font-size: 2.5vw;
        outline: none;
        border:none;
        background-color: black;
        color:white;
    }
    & button {
        width: 30%;
        height: 5vw;
        font-size: 2.5vw;
        color: rgb(0, 204, 255);
        border: none;
        outline:none
    };
}