.forecast {
    &__cards-container {
        display: flex;
        flex-direction:row;
        flex-wrap:wrap;

    }
    &__card {
        display:flex;
        flex-direction: column;
        padding: 1vw;
        box-sizing: border-box;
        width: 18vw;
        margin:1vw;
        box-shadow: 1px 1px 3px rgb(95, 95, 95);
        &--current {
            width: 40vw;
            margin-left: 10vw;
        }
        &__city {
            font-size: 2vw;
            text-align: left;
        }
        &__parameters-container {
            display:flex;
            flex-direction:row;
            justify-content: space-evenly;
        }
        
        &__time-weather {
            display:flex;
            align-items:center;
            background-color:black;
            padding: 1vh;
            height: 3.5vw;
            &__time {
                font-size: 2vw;
                margin-right: 6vw;
                color:white;

            }
            &__icon {
                font-size: 3.5vw;
                color:rgb(223, 223, 223);
                &--Rain {
                    color: rgb(0, 153, 255);
                }
                &--Clear {
                    color:rgb(255, 208, 0);
                }
                &--big {
                    font-size: 8vw;
                    margin-top: 2.7vw;
                    margin-left: 5vw;
                    text-shadow: 0.5vw 0.5vw 0.5vw rgb(0, 0, 0);
                }
            }
        }
        &__temp {
            display:flex;
            align-items:center;
            justify-content:flex-start;
            &__icon {
                font-size:2vw;
                width: 3vw;
                color:red;  
                margin-right: 0.5vw; 
            }
            &__value {
                font-size: 2.5vw;
                &:after {
                    content: "Â°C";
                    margin-left: 0.5vw;
                }
            }
        }
        &__wind {
            display:flex;
            align-items:center;
            justify-content:flex-start;
            &__icon {
                font-size:2vw;
                width: 3vw;
                color:rgb(0, 174, 255);  
                margin-right: 0.5vw; 
            }
            &__value {
                font-size: 2vw;
                &:after {
                    content: "m/s";
                    margin-left: 0.5vw;
                }
            }
        }
        &__wind-direction {
            display:flex;
            align-items:center;
            justify-content:flex-start;
            &__icon {
                font-size:2vw;
                width: 3vw;
                color:rgb(0, 255, 191);  
                margin-right: 0.5vw; 
            }
            &__value {
                font-size: 1.5vw;
                &:after {
                    content: "Deg";
                    margin-left: 0.5vw;
                }
            }
        }

    }
    &__day-container {
        display:flex;
        align-items:center;
        justify-content:center;
        margin-left: 5vw;
        & > * {
            justify-content:flex-start;
        }
        &__date {
            font-size: 1.5vw;
            width: 10vw;
        }
        &:nth-child(odd) {
            background-color: rgb(241, 241, 241);
        }
        &:nth-child(6) > * {
                justify-content:flex-start;
        }
    }
    &__day-forecasts {
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        width: 80vw;
        justify-content:flex-end;
    }
    
}